@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
:root {
  font-size: 62.5%;
}
html {
  font-family: "Red Hat Display", sans-serif;
}

body {
  background-image: url(/musicsub/static/media/pattern-background-mobile.c8d50fcb.svg);
  background-repeat: no-repeat;
  background-color: hsl(225, 100%, 94%);
  display: flex;
  justify-content: center;
}

@media (min-width: 376px) {
  body {
    background-image: url(/musicsub/static/media/pattern-background-desktop.5a0ab689.svg);
  }
}

.container_container__3NLd1 {
  background-color: #fff;
  border-radius: 2rem;
  box-shadow: 0px 2rem 4rem rgba(0, 0, 0, 0.25);
  margin: 7rem 2.5rem 7rem 2.5rem;
  max-width: 45rem;
}

@media (min-width: 768px) {
  .container_container__3NLd1 {
    margin-top: 10rem;
  }
}

.info-container_sub__14v_o {
  margin: 3.5rem 2.5rem 0 2.5rem;
  padding-bottom: 2rem;
}

.info-container_sub-description__2PlyP {
  text-align: center;
  margin-bottom: 3rem;
}

.info-container_sub-description__2PlyP h2 {
  color: hsl(223, 47%, 23%);
  font-size: 2.5rem;
  margin-bottom: 3rem;
}

.info-container_sub-description__2PlyP p {
  color: hsl(224, 23%, 55%);
  font-size: 1.6rem;
  line-height: 2.5rem;
  padding: 0 1rem;
}

.info-container_sub-info__1A6Fb {
  background-color: hsl(225, 100%, 98%);
  border-radius: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.3rem;
}

.info-container_sub-info__1A6Fb div img {
  position: relative;
  top: 0.2rem;
}
.info-container_sub-info__1A6Fb h4 {
  font-size: 1.5rem;
  font-weight: 900;
  color: hsl(223, 47%, 23%);
}

.info-container_sub-info__1A6Fb p {
  color: hsl(224, 23%, 55%);
  font-size: 1.5rem;
}

.info-container_sub-info__1A6Fb a {
  font-size: 1.3rem;
  font-weight: 700;
}

.info-container_sub-info__1A6Fb a:active {
  color: #766cf1;
  text-decoration: none;
}

.info-container_btns-container__uGMpV {
  display: flex;
  flex-direction: column;

  margin-top: 2.5rem;
}

.info-container_btn__QRg5C {
  border-style: none;
  border-radius: 1rem;
  font-family: "Red Hat Display", sans-serif;
  font-size: 1.6rem;
  padding: 1.3rem 0;
}

.info-container_payment-btn__1XJau {
  background-color: hsl(245, 75%, 52%);
  box-shadow: 0px 2rem 4rem rgba(0, 0, 0, 0.25);
  color: #fff;
  font-weight: 700;
  z-index: 10;
}

.info-container_payment-btn__1XJau:hover {
  cursor: pointer;
}

.info-container_payment-btn__1XJau:active {
  background-color: #766cf1;
}

.info-container_cancel-btn__157lQ {
  background-color: #fff;
  color: hsl(224, 23%, 55%);
  font-weight: 700;
  margin-top: 1.5rem;
}

.info-container_cancel-btn__157lQ:hover {
  cursor: pointer;
}

.info-container_cancel-btn__157lQ:active {
  color: black;
}

@media (min-width: 768px) {
  .info-container_sub__14v_o {
    margin-top: 5.5rem;
  }

  .info-container_sub-description__2PlyP p {
    padding: 0 3rem;
  }

  .info-container_sub-info__1A6Fb,
  .info-container_btns-container__uGMpV {
    margin-right: 2.8rem;
    margin-left: 2.8rem;
  }

  .info-container_sub-info__1A6Fb {
    padding: 2.5rem;
  }

  .info-container_sub-info__1A6Fb :nth-child(2) p {
    padding-top: 0.5rem;
  }

  .info-container_sub-info__1A6Fb :nth-child(3) {
    font-size: 1.4rem;
  }
}

div img {
  width: 100%;
  border-top-left-radius: 2rem;
  border-top-right-radius: 2rem;
  position: relative;
  top: -0.3rem;
}

